<template>
  <view class="records">
    <u-navbar
        isFixed
        :borderBottom="false"
        :customBack="back"
        backIconName="arrow-leftward"
        titleSize="34"
        :background="background"
        title="维修记录"></u-navbar>

    <swiper class="swiper-box" :current="curNow" @transition="transition">
      <swiper-item class="swiper-item">
        <scroll-view scroll-y style="height: 100%;width: 100%;" @scrolltolower="reachBottom">
          <view class="margin-normal radius-normal bg-white record-body">
            <nnkjc-task-item
                v-for="(record, index) in recordLists[0]"
                :taskTitle="record.seqNo"
                :taskTime="record.createTime"
                :taskContent="record.content"
                :iconClass="record.type"
                :taskType="record.type"
                :statusText="record.statusText"
                :overdueFlag="record.overdueFlag"
                :statusFlag="record.statusFlag"
                :key="index + 100">
            </nnkjc-task-item>
          </view>
          <u-empty :show="empty" text="暂无维修记录" mode="list"></u-empty>
        </scroll-view>
      </swiper-item>
    </swiper>
    <u-toast ref="uToast"/>
  </view>
</template>

<script>
  export default {
    name: "maint-record",
    data() {
      return {
        empty: false,
        background: {
          'background': '#f2f2f8'
        },
        searchParams: {
          pageSize: 10,
          pageNum: 1,
          assetId: "",
        },
        recordLists: [[]],
        curNow: 0
      }
    },
    onLoad(options) {
      this.searchParams.assetId = options.code;
    },
    onReady() {
      this.getRecordListForAll(this.curNow);
    },
    methods: {
      transition() {

      },
      reachBottom() {
        this.searchParams.pageNum++;
        this.getRecordListForAll();
      },
      back() {
        uni.navigateBack({
          delta: 1
        })
      },
      sectionChange(index) {
        this.getRecordListForAll();
      },
      //  任务列表
      getRecordListForAll() {
        this.$u
            .post("/asset/getTaskByAssetId", this.searchParams)
            .then(res => {
              if (res.data.rows.length > 0) {
                this.recordLists[0].push(...res.data.rows);
              } else {
                if (this.searchParams.pageNum !== 1) {
                  this.$refs.uToast.show({
                    title: "没有更多数据了~",
                    type: "warning"
                  });
                } else {
                  this.empty = true;
                }
                this.searchParams.pageNum--;
              }
            })
            .catch(err => {
            });
      },
    }
  }
</script>

<style scoped lang="scss">
  .records {
    height: 100%;
    display: flex;
    flex-direction: column;
    padding-bottom: 20px;
  }
  .select-wrapper {
    width: 50%;
    margin: 0 auto;
    /*paddig-top: 30px;*/
  }
  .swiper-box {
    flex-grow: 1;
  }
</style>